import { useEffect, useState } from "react";
import api from "../../utils/api";

export default function AdminDashboard() {
  const [stats, setStats] = useState({ pending: 0, approved: 0, transactions: 0 });

  useEffect(() => {
    let mounted = true;

    const fetchStats = async () => {
      try {
        const pendingRes = await api.get("/admin/pending-posts");
        const approvedRes = await api.get("/admin/approved-posts");
        // যদি transaction API না থাকে, তাহলে নিচের লাইনটা comment করে রাখো
        // const txRes = await api.get("/admin/transactions");
        const txCount = 0; // api না থাকলে temporary fix

        const pendingCount = pendingRes.data?.length || 0;
        const approvedCount = approvedRes.data?.length || 0;

        if (mounted)
          setStats({ pending: pendingCount, approved: approvedCount, transactions: txCount });
      } catch (err) {
        console.error("Error fetching stats:", err);
      }
    };

    fetchStats();
    return () => {
      mounted = false;
    };
  }, []);

  return (
    <div className="min-h-screen bg-gray-50 p-10">
      <h1 className="text-3xl font-bold text-gray-800 mb-8">Admin Dashboard</h1>
      <div className="grid md:grid-cols-3 gap-6">
        <div className="bg-white rounded-2xl shadow p-6 text-center">
          <h2 className="text-xl font-semibold text-gray-700">Pending Posts</h2>
          <p className="text-3xl font-bold text-indigo-600 mt-3">{stats.pending}</p>
        </div>
        <div className="bg-white rounded-2xl shadow p-6 text-center">
          <h2 className="text-xl font-semibold text-gray-700">Approved Posts</h2>
          <p className="text-3xl font-bold text-green-600 mt-3">{stats.approved}</p>
        </div>
        <div className="bg-white rounded-2xl shadow p-6 text-center">
          <h2 className="text-xl font-semibold text-gray-700">Transactions</h2>
          <p className="text-3xl font-bold text-blue-600 mt-3">{stats.transactions}</p>
        </div>
      </div>
    </div>
  );
}
